.rmdb-grid h1 {
    font-family: 'Abel', sans-serif;
    font-size:22px;
  }
  
  .rmdb-grid-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 5px;
    grid-row-gap: 20px; 
  }
  
  .rmdb-grid-element {
    background: #353535;
    max-height: 400px;
    animation: animateGrid 0.5s;
    overflow: hidden;
  }
  
  @keyframes animateGrid {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
  }
  
  @media screen and (max-width: 720px) {
    .rmdb-grid-content {
      grid-template-columns: auto auto;
    }
  
    .rmdb-grid-element:nth-child(2n+2) {
      margin-right: 0;
    }
  
    .rmdb-grid h1 {
      font-size:12px;
    }
  }